/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Input, Tag, Image } from "antd";
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from "@ant-design/icons";
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from "react";
import Pagination from "../../components/Pagination";
import { AntdMsg } from "../../../utils/Antd";
import service from "../../../services/team";
import UploadImage from "../../../utils/UploadImage";

export default function Team() {
    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);

    const [qData, setQData] = useState({ page: 1, limit: 20, });
    const addNewModalRef = useRef();
    const columns = [
        {
            title: "#",
            width: 40,
            render: (v, row, i) => <b>{i + 1}</b>,
        },
        {
            title: "Name",
            dataIndex: "name",
        },
        {
            title: "Designation",
            dataIndex: "designation",
            width: 250,
        },
        {
            title: "Status",
            dataIndex: "status",
            width: 80,
            render: (isActive) => {
                if (isActive) {
                    return <Tag color="green">Active</Tag>;
                } else {
                    return <Tag color="red">Inactive</Tag>;
                }
            },
        },
        {
            title: "Action",
            dataIndex: "_id",
            width: 70,
            render: (v, row) => {
                return (
                    <>
                        <Popconfirm
                            title="Are you sure to delete this data?"
                            onConfirm={() => {
                                deleteData(row._id);
                            }}
                            okText="Yes"
                            cancelText="No"
                        >
                            <Button
                                type="primary"
                                size="small"
                                danger
                                icon={<DeleteOutlined />}
                            />
                            &nbsp;
                        </Popconfirm>
                        <Button
                            type="primary"
                            size="small"
                            icon={<EditOutlined />}
                            onClick={() => {
                                addNewModalRef.current.openForm(row);
                            }}
                        />
                    </>
                );
            },
        },
    ].filter((item) => !item.hidden);
    function list() {
        setLoading(true);
        service
            .list(qData)
            .then((res) => {
                setData(res.data?.map((v) => ({ ...v, key: v._id })));
                setQData({
                    ...qData,
                    limit: res.extra.limit,
                    page: res.extra.page,
                    total: res.extra.total,
                });
            })
            .catch((err) => { })
            .finally(() => {
                setLoading(false);
            });
    }
    const deleteData = (id) => {
        service
            .delete(id)
            .then((res) => {
                AntdMsg(res.message);
                list();
                setSelected([]);
            })
            .catch((err) => {
                AntdMsg(err.message, "error");
            });
    };
    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));
    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = "right";
    const tableProps = {
        bordered: true,
        loading,
        size: "small",
        title: () => (
            <Search
                {...{ addNewModalRef, selected, deleteData, qData, setQData, list }}
            />
        ),
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };
    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);
    return (
        <>
            <Typography.Title level={5} style={{ marginTop: 0 }}>
                Team List
            </Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ["none"], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: "calc(100vh - 340px)", x: "calc(100vw - 387px)" }}
            />
            <AddForm
                ref={addNewModalRef}
                {...{
                    list,
                }}
            />
        </>
    );
}
function Search({ addNewModalRef, selected, deleteData, qData, list, }) {
    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined || v === "") {
            delete qData[key];
            list();
        }
    };
    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input
                            placeholder="Search by name"
                            value={qData.key}
                            onChange={(e) => onChange(e.target.value, "key")}
                            allowClear
                        />
                    </Form.Item>
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">
                            Search
                        </Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: "right" }}>
                        <Button
                            type="primary"
                            icon={<PlusOutlined />}
                            onClick={() => {
                                addNewModalRef.current.openForm();
                            }}
                        >
                            Add New
                        </Button>
                    </Form.Item>
                    {selected.length ? (
                        <Popconfirm
                            title="Are you sure to delete these selected data ?"
                            onConfirm={() => {
                                deleteData(selected);
                            }}
                            okText="Yes"
                            cancelText="No"
                        >
                            <Button
                                type="primary"
                                danger
                                style={{ float: "right" }}
                                icon={<DeleteOutlined />}
                            >
                                Delete Selected
                            </Button>
                        </Popconfirm>
                    ) : null}
                </Col>
            </Row>
        </Form>
    );
}
const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [file, setFile] = useState([]);

    const handleChange = (value) => {
        setData({ ...data, ...value });
    };
    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(dt ? { ...dt } : { });
            if(dt?.img){
                setFile([dt.img]);
            } else{
                setFile([]);
            }
        }
    }));

    const save = () => {
        setAjxRequesting(true);
        service
            .save(data)
            .then((res) => {
                AntdMsg(res.message);
                handleChange({ ...res.data });
                list();
                setOpen(false);
            })
            .catch((err) => {
                if (typeof err.message === "object") {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, "error");
                } else {
                    AntdMsg(err.message, "error");
                }
            })
            .finally(() => {
                setAjxRequesting(false);
            });
    };

    useEffect(() => {
        setData({ ...data, img: file });
    }, [file]);

    return (
        <>
            <Modal
                title={(!data._id ? "Add New" : "Edit") + " Team Member"}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => {
                    setOpen(false);
                }}
                destroyOnClose
                maskClosable={false}
                width={500}
                className="app-modal-without-footer-body-overflow1"
                footer={[
                    <Button
                        key="cancel"
                        onClick={() => {
                            setOpen(false);
                        }}
                    >
                        Cancel
                    </Button>,
                    <Button key="save" type="primary" onClick={save}>
                        Save
                    </Button>,
                ]}
            >
                <Spin
                    spinning={ajxRequesting}
                    indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}
                >
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={24}>
                                <Form.Item label="Name" required>
                                    <Input
                                        placeholder="John"
                                        value={data.name}
                                        onChange={(e) => {
                                            handleChange({
                                                name: e.target.value
                                            });
                                        }}
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Designation" required>
                                    <Input
                                        placeholder="Executive manager"
                                        value={data.designation}
                                        onChange={(e) => {
                                            handleChange({ designation: e.target.value });
                                        }}
                                    />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Status" required>
                                    <Select
                                        value={data.status}
                                        onChange={(e) => {
                                            handleChange({ status: e });
                                        }}
                                    >
                                        <Select.Option value={true}>Active</Select.Option>
                                        <Select.Option value={false}>Inactive</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Photo">
                                    <UploadImage
                                        {...{ fileList: file, setFileList: setFile, count: 1, listType: 'picture' }}
                                    />
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});