/* eslint-disable react-hooks/exhaustive-deps */
import {
  Table,
  Button,
  Form,
  Row,
  Col,
  Popconfirm,
  Modal,
  Spin,
  Typography,
  Select,
  Input,
  Tag,
} from "antd";
import {
  DeleteOutlined,
  EditOutlined,
  LoadingOutlined,
  PlusOutlined,
} from "@ant-design/icons";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
  useRef,
} from "react";
import Pagination from "../../components/Pagination";
import { AntdMsg } from "../../../utils/Antd";
import service from "../../../services/job";
import util from "../../../utils/util";

export default function Jobs({ parentId }) {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [selected, setSelected] = useState([]);
  const [qData, setQData] = useState({
    page: 1,
    limit: 20,
    fileType: "photo",
    parentId,
  });
  const addNewModalRef = useRef();
  const addNewLabelRef = useRef();
  const columns = [
    {
      title: "#",
      width: 40,
      render: (v, row, i) => <b>{i + 1}</b>,
    },
    {
      title: "Designation",
      dataIndex: "name",
    },
    // {
    //     title: 'Button',
    //     dataIndex: 'button',
    //     width: 100,
    //     render: (value, row) => { return <Button onClick={() => { addNewLabelRef.current.openForm(row) }}>Active</Button> },

    // },
    {
      title: "Skill",
      dataIndex: "skill",
      // width: 100,
    },
    {
      title: "Location",
      dataIndex: "location",
      // width: 100,
    },
    {
      title: "Experience",
      dataIndex: "experience",
      // width: 100,
    },
    {
      title: "Description",
      dataIndex: "description",
      // width: 100,
    },
    {
      title: "Status",
      dataIndex: "status",
      width: 80,
      render: (isActive) => {
        if (isActive) {
          return <Tag color="green">Open</Tag>;
        } else {
          return <Tag color="red">Close</Tag>;
        }
      },
    },
    {
      title: "Action",
      dataIndex: "_id",
      width: 70,
      render: (v, row) => {
        return (
          <>
            <Popconfirm
              title="Are you sure to delete this data?"
              onConfirm={() => {
                deleteData(row._id);
              }}
              okText="Yes"
              cancelText="No"
            >
              <Button
                type="primary"
                size="small"
                danger
                icon={<DeleteOutlined />}
              />
              &nbsp;
            </Popconfirm>
            <Button
              type="primary"
              size="small"
              icon={<EditOutlined />}
              onClick={() => {
                addNewModalRef.current.openForm(row._id);
              }}
            />
          </>
        );
      },
    },
  ].filter((item) => !item.hidden);
  function list() {
    setLoading(true);
    service
      .listUser(qData)
      .then((res) => {
        setData(res.data?.map((v) => ({ ...v, key: v._id })));
        setQData({
          ...qData,
          limit: res.extra.limit,
          page: res.extra.page,
          total: res.extra.total,

        });
      })
      .catch((err) => {})
      .finally(() => {
        setLoading(false);
      });
  }
  const deleteData = (id) => {
    service
      .deleteUser(id)
      .then((res) => {
        AntdMsg(res.message);
        list();
        setSelected([]);
      })
      .catch((err) => {
        AntdMsg(err.message, "error");
      });
  };
  const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));
  tableColumns[0].fixed = true;
  tableColumns[tableColumns.length - 1].fixed = "right";
  const tableProps = {
    bordered: true,
    loading,
    size: "small",
    title: () => (
      <Search
        {...{ addNewModalRef, selected, deleteData, qData, setQData, list }}
      />
    ),
    showHeader: true,
    footer: () => <Pagination {...{ qData, setQData }} />,
    rowSelection: {
      onChange: (selectedRowKeys) => {
        setSelected(selectedRowKeys);
      },
    },
    tableLayout: undefined,
  };
  useEffect(() => {
    list();
  }, [qData.page, qData.limit]);
  return (
    <>
      {!parentId ? (
        <Typography.Title level={5} style={{ marginTop: 0 }}>
          List of Jobs
        </Typography.Title>
      ) : null}
      <Table
        {...tableProps}
        pagination={{ position: ["none"], pageSize: qData.limit }}
        columns={tableColumns}
        dataSource={data.length ? data : []}
        scroll={{ y: "calc(100vh - 340px)", x: "calc(100vw - 387px)" }}
      />
      <AddForm ref={addNewModalRef} {...{ list, parentId }} />
      <AddLabel ref={addNewLabelRef} {...{ list }} />
    </>
  );
}
function Search({
  addNewModalRef,
  selected,
  deleteData,
  qData,
  setQData,
  list,
}) {
  const onChange = (v, key) => {
    qData[key] = v;
    if (v === undefined || v === "") {
      delete qData[key];
      list();
    }
  };
  return (
    <Form onSubmitCapture={list} className="search-form">
      <Row gutter={[12, 2]}>
        <Col span={4}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Input
              placeholder="Search by Designation"
              value={qData.key}
              onChange={(e) => onChange(e.target.value, "key")}
              allowClear
            />
          </Form.Item>
        </Col>
        <Col span={3}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Button type="primary" htmlType="submit">
              Search
            </Button>
          </Form.Item>
        </Col>
        <Col span={5}>
          <Form.Item style={{ marginBottom: 0, float: "right" }}>
            <Button
              type="primary"
              icon={<PlusOutlined />}
              onClick={() => {
                addNewModalRef.current.openForm();
              }}
            >
              Add New
            </Button>
          </Form.Item>
          {selected.length ? (
            <Popconfirm
              title="Are you sure to delete these selected data ?"
              onConfirm={() => {
                deleteData(selected);
              }}
              okText="Yes"
              cancelText="No"
            >
              <Button
                type="primary"
                danger
                style={{ float: "right" }}
                icon={<DeleteOutlined />}
              >
                Delete Selected
              </Button>
            </Popconfirm>
          ) : null}
        </Col>
      </Row>
    </Form>
  );
}
const AddForm = forwardRef((props, ref) => {
  const { list, parentId } = props;
  const [open, setOpen] = useState(false);
  const [data, setData] = useState({ aspect: 1 });
  const [ajxRequesting, setAjxRequesting] = useState(false);

  const handleChange = (value) => {
    setData({ ...data, ...value });
  };
  useImperativeHandle(ref, () => ({
    openForm(dt) {
      setOpen(true);
      setData(dt ? { _id: dt } : { parentId });
    },
  }));

  const getDetails = () => {
    setAjxRequesting(true);
    service
      .detailUser(data._id)
      .then((res) => {
        setData(res.data || {});
      })
      .catch((err) => {
        AntdMsg(err.message, "error");
      })
      .finally(() => {
        setAjxRequesting(false);
      });
  };
  const save = () => {
    setAjxRequesting(true);
    service
      .saveUser(data)
      .then((res) => {
        AntdMsg(res.message);
        handleChange({ ...res.data });
        list();
        setOpen(false);
      })
      .catch((err) => {
        if (typeof err.message === "object") {
          let dt = err.message[Object.keys(err.message)[0]];
          AntdMsg(dt, "error");
        } else {
          AntdMsg(err.message, "error");
        }
      })
      .finally(() => {
        setAjxRequesting(false);
      });
  };
  useEffect(() => {
    if (data._id) {
      getDetails();
    }
  }, [data._id]);

  useEffect(() => {
    if (!open) {
      setData({ _id: null });
    }
  }, [open]);
  useEffect(() => {
    if (!data._id) {
      handleChange({ slug: util.removeSpecialChars(data.name || "") });
    }
  }, [data.name]);
  return (
    <>
      <Modal
        title={(!data._id ? "Add" : "Edit") + " Job Data"}
        style={{ top: 20 }}
        open={open}
        okText="Save"
        onOk={save}
        okButtonProps={{ disabled: ajxRequesting }}
        onCancel={() => {
          setOpen(false);
        }}
        destroyOnClose
        maskClosable={false}
        width={800}
        className="app-modal-body"
        footer={[
          <Button
            key="cancel"
            onClick={() => {
              setOpen(false);
            }}
          >
            Cancel
          </Button>,
          <Button key="save" type="primary" onClick={save}>
            Save
          </Button>,
        ]}
      >
        <Spin
          spinning={ajxRequesting}
          indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}
        >
          <Form layout="vertical">
            <Row gutter={[12, 2]}>
              <Col span={16}>
                <Form.Item label="Designation" required>
                  <Input
                    placeholder="Designation"
                    value={data.name}
                    onChange={(e) => {
                      handleChange({ name: e.target.value });
                    }}
                  />
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item label="Skill" required>
                  <Input
                    placeholder="Skill"
                    value={data.skill}
                    onChange={(e) => {
                      handleChange({ skill: e.target.value });
                    }}
                  />
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Location" required>
                  <Input
                    placeholder="Location"
                    value={data.location}
                    onChange={(e) => {
                      handleChange({ location: e.target.value });
                    }}
                  />
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Experience" required>
                  <Input
                    placeholder="Experience"
                    value={data.experience}
                    onChange={(e) => {
                      handleChange({ experience: e.target.value });
                    }}
                  />
                </Form.Item>
              </Col>
              <Col span={24}>
                <Form.Item label="Description" required>
                  <Input.TextArea
                    placeholder="Description"
                    value={data.description}
                    onChange={(e) => {
                      handleChange({ description: e.target.value });
                    }}
                  />
                </Form.Item>
              </Col>
              <Col span={8}>
                <Form.Item label="Status" required>
                  <Select
                    value={data.status}
                    onChange={(e) => {
                      handleChange({ status: e });
                    }}
                  >
                    <Select.Option value={true}>Open</Select.Option>
                    <Select.Option value={false}>Close</Select.Option>
                  </Select>
                </Form.Item>
              </Col>
            </Row>
          </Form>
        </Spin>
      </Modal>
    </>
  );
});

const AddLabel = forwardRef((props, ref) => {
  const [open, setOpen] = useState(false);
  const [data, setData] = useState({ aspect: 1 });
  // const [ajxRequesting, setAjxRequesting] = useState(false);

  useImperativeHandle(ref, () => ({
    openForm(dt) {
      setOpen(true);
      setData({ ...dt });
    },
  }));

  return (
    <>
      <Modal
        title={(!data._id ? "Add" : "Edit") + " Opportunity"}
        style={{ top: 20 }}
        open={open}
        okText="Save"
        // onOk={save}
        // okButtonProps={{ disabled: ajxRequesting }}
        onCancel={() => {
          setOpen(false);
        }}
        destroyOnClose
        maskClosable={false}
        width={1200}
        className="app-modal-body"
        // footer={[
        //     <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
        //     <Button key="save" type="primary" onClick={save}>Save</Button>,
        // ]}
      >
        {/* <Gases parentId={data._id} /> */}
      </Modal>
    </>
  );
});
