/* eslint-disable react-hooks/exhaustive-deps */
import {
  Table,
  Button,
  Form,
  Row,
  Col,
  Popconfirm,
  Modal,
  Spin,
  Typography,
  Select,
  Input,
  Tag,
} from "antd";
import {
  DeleteOutlined,
  EditOutlined,
  LoadingOutlined,
  PlusOutlined,
} from "@ant-design/icons";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
  useRef,
} from "react";
import Pagination from "../components/Pagination";
import { AntdMsg } from "../../utils/Antd";
import service from "../../services/homepageproduct";
import serviceProduct from "../../services/product";
import offerService from "../../services/Offer";

export default function Product() {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [selected, setSelected] = useState([]);
  const [qData, setQData] = useState({ page: 1, limit: 20 });
  const addNewModalRef = useRef();
  const [allProduct, setAllProduct] = useState([]);

  const columns = [
    {
      title: "#",
      width: 40,
      render: (v, row, i) => <b>{i + 1}</b>,
    },
    {
      title: "Row",
      dataIndex: "row",
      width: 50,
    },
    {
      title: "Products",
      dataIndex: "productDetails",
      render: (v) => v.map((vv) => <Tag>{vv.name}</Tag>),
    },
    {
      title: "Left Offer",
      dataIndex: "leftOfferDetails",
	  width:250,
      render: (v) => <Tag>{v?.title}</Tag>,
    },
    {
      title: "Right Offer",
      dataIndex: "rightOfferDetails",
	  width:250,
      render: (v) => <Tag>{v?.title}</Tag>,
    },
    {
      title: "Action",
      dataIndex: "_id",
      width: 70,
      render: (v, row) => {
        return (
          <>
            <Popconfirm
              title="Are you sure to delete this data?"
              onConfirm={() => {
                deleteData(row._id);
              }}
              okText="Yes"
              cancelText="No"
            >
              <Button
                type="primary"
                size="small"
                danger
                icon={<DeleteOutlined />}
              />
              &nbsp;
            </Popconfirm>
            <Button
              type="primary"
              size="small"
              icon={<EditOutlined />}
              onClick={() => {
                addNewModalRef.current.openForm(row);
              }}
            />
          </>
        );
      },
    },
  ].filter((item) => !item.hidden);
  function list() {
    setLoading(true);
    service
      .list(qData)
      .then((res) => {
        setData(res.data?.map((v) => ({ ...v, key: v._id })));
        setQData({
          ...qData,
          limit: res.extra.limit,
          page: res.extra.page,
          total: res.extra.total,
        });
      })
      .catch((err) => {})
      .finally(() => {
        setLoading(false);
      });
  }
  useEffect(() => {
    list();
  }, []);
  const deleteData = (id) => {
    service
      .delete(id)
      .then((res) => {
        AntdMsg(res.message);
        list();
        setSelected([]);
      })
      .catch((err) => {
        AntdMsg(err.message, "error");
      });
  };
  const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));
  tableColumns[0].fixed = true;
  tableColumns[tableColumns.length - 1].fixed = "right";
  const tableProps = {
    bordered: true,
    loading,
    size: "small",
    title: () => (
      <Search
        {...{ addNewModalRef, selected, deleteData, qData, setQData, list }}
      />
    ),
    showHeader: true,
    footer: () => <Pagination {...{ qData, setQData }} />,
    rowSelection: {
      onChange: (selectedRowKeys) => {
        setSelected(selectedRowKeys);
      },
    },
    tableLayout: undefined,
  };
  useEffect(() => {
    serviceProduct.listUser({ isAll: 1 }).then((res) => {
      setAllProduct(
        res.data.map((v) => ({ ...v, label: v.name, value: v._id }))
      );
    });
  }, [qData.page, qData.limit]);
  return (
    <>
      <Typography.Title level={5} style={{ marginTop: 0 }}>
        Product List
      </Typography.Title>
      <Table
        {...tableProps}
        pagination={{ position: ["none"], pageSize: qData.limit }}
        columns={tableColumns}
        dataSource={data.length ? data : []}
        scroll={{ y: "calc(100vh - 340px)", x: "calc(100vw - 387px)" }}
      />
      <AddForm ref={addNewModalRef} {...{ list, allProduct }} />
    </>
  );
}

function Search({
  addNewModalRef,
  selected,
  deleteData,
  qData,
  setQData,
  list,
}) {
  return (
    <Form onSubmitCapture={list} className="search-form">
      <Row gutter={[12, 2]}>
        <Col span={4}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Input
              placeholder="Search by name or code"
              value={qData.key}
              onChange={(e) => setQData({ ...qData, key: e.target.value })}
              allowClear
            />
          </Form.Item>
        </Col>
        <Col span={3}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Button type="primary" htmlType="submit">
              Search
            </Button>
          </Form.Item>
        </Col>
        <Col span={5}>
          <Form.Item style={{ marginBottom: 0, float: "right" }}>
            <Button
              type="primary"
              icon={<PlusOutlined />}
              onClick={() => {
                addNewModalRef.current.openForm();
              }}
            >
              Add New
            </Button>
          </Form.Item>
          {selected.length ? (
            <Popconfirm
              title="Are you sure to delete these selected data ?"
              onConfirm={() => {
                deleteData(selected);
              }}
              okText="Yes"
              cancelText="No"
            >
              <Button
                type="primary"
                danger
                style={{ float: "right" }}
                icon={<DeleteOutlined />}
              >
                Delete Selected
              </Button>
            </Popconfirm>
          ) : null}
        </Col>
      </Row>
    </Form>
  );
}

const AddForm = forwardRef((props, ref) => {
  const { list, allProduct } = props;
  const [open, setOpen] = useState(false);
  const [data, setData] = useState({ aspect: 1 });
  const [ajxRequesting, setAjxRequesting] = useState(false);
  const [offer, setOffer] = useState();

  const handleChange = (value) => {
    setData({ ...data, ...value });
  };
  useImperativeHandle(ref, () => ({
    openForm(dt) {
      setOpen(true);
      setData(dt ? { ...dt } : {});
    },
  }));

  const save = () => {
    setAjxRequesting(true);
    service
      .save(data)
      .then((res) => {
        AntdMsg(res.message);
        handleChange({ ...res.data });
        list();
        setOpen(false);
      })
      .catch((err) => {
        if (typeof err.message === "object") {
          let dt = err.message[Object.keys(err.message)[0]];
          AntdMsg(dt, "error");
        } else {
          AntdMsg(err.message, "error");
        }
      })
      .finally(() => {
        setAjxRequesting(false);
      });
  };

  const fetch = () => {
    offerService
      .list()
      .then((res) =>
        setOffer(res.data.map((v) => ({ label: v.title, value: v._id })))
      )
      .catch((err) => AntdMsg(err, "error"));
  };
  useEffect(() => {
    if (!open) {
      setData({});
    }
    fetch();
    console.log(offer);
  }, [open]);

  return (
    <>
      <Modal
        title={(!data._id ? "Add New" : "Edit") + " Product"}
        style={{ top: 20 }}
        open={open}
        okText="Save"
        onOk={save}
        okButtonProps={{ disabled: ajxRequesting }}
        onCancel={() => {
          setOpen(false);
        }}
        destroyOnClose
        maskClosable={false}
        width={1000}
        className="app-modal-body"
        footer={[
          <Button
            key="cancel"
            onClick={() => {
              setOpen(false);
            }}
          >
            Cancel
          </Button>,
          <Button key="save" type="primary" onClick={save}>
            Save
          </Button>,
        ]}
      >
        <Spin
          spinning={ajxRequesting}
          indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}
        >
          <Form layout="vertical">
            <Row gutter={[12, 2]}>
              <Col span={6}>
                <Form.Item label="Row Number" required>
                  <Input
                    type="number"
                    min={1}
                    max={5}
                    placeholder="Row Number.."
                    value={data.row}
                    onChange={(e) => {
                      handleChange({ row: e.target.value });
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={24}>
                <Form.Item label="Choose Product" required>
                  <Select
                    mode="multiple"
                    placeholder="Choose Products"
                    onChange={(v) => {
                      handleChange({ productIds: v });
                    }}
                    optionLabelProp="label"
                    allowClear={true}
                    value={data.productIds}
                    options={allProduct.filter((v) => {
                      return true;
                    })}
                  />
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Left Offer">
                  <Select
                    style={{
                      width: "100%",
                    }}
                    placeholder="Select Left Offer"
                    onChange={(v) => {
                      handleChange({ leftOffer: v });
                    }}
                    value={data.leftOffer}
                    optionLabelProp="label"
                    allowClear={true}
                    options={offer || []}
                  />
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Right Offer">
                  <Select
                    style={{
                      width: "100%",
                    }}
                    placeholder="Select Right Offer"
                    onChange={(v) => {
                      handleChange({ rightOffer: v });
                    }}
                    value={data.rightOffer}
                    optionLabelProp="label"
                    allowClear={true}
                    options={offer || []}
                  />
                </Form.Item>
              </Col>
            </Row>
          </Form>
        </Spin>
      </Modal>
    </>
  );
});
