/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Input, Radio, Image }  from "antd";
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from "@ant-design/icons";
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from "react";
import Pagination from "../components/Pagination";
import { AntdMsg } from "../../utils/Antd";
import service from "../../services/aboutus";
import UploadImage from "../../utils/UploadImage";

export default function Aboutus() {
    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20 });
    const addNewModalRef = useRef();
    const columns = [
        {
            title: "Column No",
            width: 100,
            dataIndex: "columnNo",
        },
        {
            title: "Image",
            dataIndex: "img",
            width: 100,
            render: (v) => {
                return <Image src={v?.url} height={40} />;
            },
        },
        {
            title: "Title",
            dataIndex: "title",
            width: 150,
        },
        {
            title: "Description",
            dataIndex: "description",
            // width: 250,
            render: (v) => (
                <Typography.Text ellipsis={{ expandable: true }}>{v}</Typography.Text>
            ),
        },
        {
            title: "Action",
            dataIndex: "_id",
            width: 70,
            render: (v, row) => {
                return (
                    <>
                        <Popconfirm
                            title="Are you sure to delete this data?"
                            onConfirm={() => {
                                deleteData(row._id);
                            }}
                            okText="Yes"
                            cancelText="No"
                        >
                            <Button
                                type="primary"
                                size="small"
                                danger
                                icon={<DeleteOutlined />}
                            />
                            &nbsp;
                        </Popconfirm>
                        <Button
                            type="primary"
                            size="small"
                            icon={<EditOutlined />}
                            onClick={() => {
                                addNewModalRef.current.openForm(row);
                            }}
                        />
                    </>
                );
            },
        },
    ].filter((item) => !item.hidden);
    function list() {
        setLoading(true);
        service
            .list(qData)
            .then((res) => {
                setData(res.data?.map((v) => ({ ...v, key: v._id })));
                setQData({
                    ...qData,
                    limit: res.extra.limit,
                    page: res.extra.page,
                    total: res.extra.total,
                });
            })
            .catch((err) => { })
            .finally(() => {
                setLoading(false);
            });
    }
    const deleteData = (id) => {
        service
            .delete(id)
            .then((res) => {
                AntdMsg(res.message);
                list();
                setSelected([]);
            })
            .catch((err) => {
                AntdMsg(err.message, "error");
            });
    };
    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));
    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = "right";
    const tableProps = {
        bordered: true,
        loading,
        size: "small",
        title: () => (
            <Search
                {...{ addNewModalRef, selected, deleteData, qData, setQData, list }}
            />
        ),
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };
    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);
    return (
        <>
                <Typography.Title level={5} style={{ marginTop: 0 }}>
                    Product List
                </Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ["none"], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: "calc(100vh - 340px)", x: "calc(100vw - 387px)" }}
            />
            <AddForm
                ref={addNewModalRef}
                {...{
                    list,
                }}
            />
        </>
    );
}
function Search({  addNewModalRef, selected, deleteData, qData, setQData, list, }) {
    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined || v === "") {
            delete qData[key];
            list();
        }
    };
    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: "right" }}>
                        <Button
                            type="primary"
                            icon={<PlusOutlined />}
                            onClick={() => {
                                addNewModalRef.current.openForm();
                            }}
                        >
                            Add New
                        </Button>
                    </Form.Item>
                    {selected.length ? (
                        <Popconfirm
                            title="Are you sure to delete these selected data ?"
                            onConfirm={() => {
                                deleteData(selected);
                            }}
                            okText="Yes"
                            cancelText="No"
                        >
                            <Button
                                type="primary"
                                danger
                                style={{ float: "right" }}
                                icon={<DeleteOutlined />}
                            >
                                Delete Selected
                            </Button>
                        </Popconfirm>
                    ) : null}
                </Col>
            </Row>
        </Form>
    );
}
const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1, type: "text" });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [thumbFile, setThumbFile] = useState([]);

    const handleChange = (value) => {
        setData({ ...data, ...value });
    };
    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(dt ? { ...dt } : {});
            console.log(dt);
            if(dt.img){
                setThumbFile([dt.img]);
            } else{
                setThumbFile([]);
            }
        },
    }));
    const save = () => {
        setAjxRequesting(true);
        service
            .save(data)
            .then((res) => {
                AntdMsg(res.message);
                handleChange({ ...res.data });
                list();
                setOpen(false);
            })
            .catch((err) => {
                if (typeof err.message === "object") {
                    let dt = err.message[Object.keys(err.message)[0]];
                    AntdMsg(dt, "error");
                } else {
                    AntdMsg(err.message, "error");
                }
            })
            .finally(() => {
                setAjxRequesting(false);
            });
    };

    useEffect(() => {
        setData({ ...data, img: thumbFile[0] });
    }, [thumbFile]);

    return (
        <>
            <Modal
                title={(!data._id ? "Add " : "Edit") + ""}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => {
                    setOpen(false);
                }}
                destroyOnClose
                maskClosable={false}
                width={600}
                className="app-modal-without-footer-body-overflow1"
                footer={[
                    <Button
                        key="cancel"
                        onClick={() => {
                            setOpen(false);
                        }}
                    >
                        Cancel
                    </Button>,
                    <Button key="save" type="primary" onClick={save}>
                        Save
                    </Button>,
                ]}
            >
                <Spin
                    spinning={ajxRequesting}
                    indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}
                >
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={24}>
                                <Form.Item label="Select Type: " required>
                                    <Radio.Group
                                        buttonStyle="solid"
                                        value={data.type}
                                        defaultValue={"image"}
                                        onChange={(e) => handleChange({ type: e.target.value })}
                                    >
                                        <Radio.Button value="image">Image</Radio.Button>
                                        <Radio.Button value="text">Text</Radio.Button>
                                    </Radio.Group>
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Column No." required>
                                    <Input
                                        placeholder="1"
                                        value={data.columnNo}
                                        onChange={(e) => {
                                            handleChange({ columnNo: e.target.value });
                                        }}
                                    />
                                </Form.Item>
                            </Col>
                            {data.type === "text" ? (
                                <>
                                    <Col span={24}>
                                        <Form.Item label="Title" required>
                                            <Input
                                                placeholder="How we work ??"
                                                value={data.title}
                                                onChange={(e) => {
                                                    handleChange({ title: e.target.value });
                                                }}
                                            />
                                        </Form.Item>
                                    </Col>
                                    <Col span={24}>
                                        <Form.Item label="Description" required>
                                            <Input.TextArea
                                                rows={8}
                                                placeholder="We are a great team of ....."
                                                value={data.description}
                                                onChange={(e) => {
                                                    handleChange({ description: e.target.value });
                                                }}
                                            />
                                        </Form.Item>
                                    </Col>
                                </>
                            ) : (
                                <Col span={24}>
                                    <Form.Item label="Image">
                                        <UploadImage
                                            {...{ fileList: thumbFile, setFileList: setThumbFile, listType: 'picture' }}
                                        />
                                    </Form.Item>
                                </Col>
                            )}
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});
