/* eslint-disable react-hooks/exhaustive-deps */
import {
  Table,
  Button,
  Form,
  Input,
  Row,
  Col,
  Popconfirm,
  Modal,
  Spin,
  Typography,
  Select,
  Tag,
} from "antd";
import {
  DeleteOutlined,
  EditOutlined,
  LoadingOutlined,
  EyeOutlined,
} from "@ant-design/icons";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
  useRef,
} from "react";
import service from "../../../services/orders";
import Pagination from "../../components/Pagination";
import { AntdMsg } from "../../../utils/Antd";
import OrderDetail from "./OrderDetail";

export default function Order({ type, categoryId = null }) {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [selected, setSelected] = useState([]);
  const [qData, setQData] = useState({ page: 1, limit: 20, type, categoryId });
  const attributeModalRef = useRef();

  const columns = [
    {
      title: "Order id",
      dataIndex: "orderId",
      // width: 200,
    },
    {
      title: "Shipping Address",
      dataIndex: "addressDetails",
      width: 250,
      render: (_) => {
        const data = JSON.parse(_);
        return <span>{data.address + " " + data.state + " " + data.pin}</span>;
      },
    },
    {
      title: "Total Amount",
      dataIndex: "priceDetails",
      width: 140,
      render: (_) => {
        return <span>{_.finalPrice}</span>;
      },
    },
    {
      title: "Details",
      dataIndex: "orderId",
      width: 150,
      render: (value, row) => (
        <Button
          type="primary"
          size="small"
          onClick={() => {
            attributeModalRef.current.openForm({ _id: row._id });
          }}
        >
          Order Details
        </Button>
      ),
    },
    {
      title: "Status",
      dataIndex: "status",
      width: 200,
      render: (_, { status }) => {
        return <Tag color="green">{status}</Tag>;
      },
    },
  ].filter((item) => !item.hidden);

  function list() {
      setLoading(true);
      service
        .orderList(qData)
        .then((res) => {
          setData(res.data?.map((v) => ({ ...v, key: v._id })));
          setQData({
            ...qData,
            limit: res.extra.limit,
            page: res.extra.page,
            total: res.extra.total,
          });
        })
        .catch((err) => {})
        .finally(() => {
          setLoading(false);
        });
  }

  const deleteData = (id) => {
    service
      .orderDelete(id)
      .then((res) => {
        AntdMsg(res.message);
        list();
        setSelected([]);
      })
      .catch((err) => {
        AntdMsg(err.message, "error");
      });
  };

  const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

  tableColumns[0].fixed = true;
  tableColumns[tableColumns.length - 1].fixed = "right";

  const tableProps = {
    bordered: true,
    loading,
    size: "small",
    title: () => (
      <Search
        {...{
          selected,
          deleteData,
          qData,
          setQData,
          list,
          categoryId,
        }}
      />
    ),
    showHeader: true,
    footer: () => <Pagination {...{ qData, setQData }} />,
    rowSelection: {
      onChange: (selectedRowKeys) => {
        setSelected(selectedRowKeys);
      },
    },
    tableLayout: undefined,
  };

  useEffect(() => {
    list();
  }, [qData.page, qData.limit]);

  return (
    <>
      {!categoryId ? (
        <Typography.Title level={5}>List of Orders</Typography.Title>
      ) : null}

      <Table
        {...tableProps}
        pagination={{ position: ["none"], pageSize: qData.limit }}
        columns={tableColumns}
        dataSource={data.length ? data : []}
        scroll={{ y: "calc(100vh - 340px)", x: "calc(100vw - 387px)" }}
      />
      <AttributeModal ref={attributeModalRef} />
    </>
  );
}

function Search({ selected, deleteData, qData, setQData, list }) {
  return (
    <Form onSubmitCapture={list} className="search-form">
      <Row gutter={[12, 2]}>
        <Col span={4}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Input
              placeholder="Search by order id"
              value={qData.key}
              onChange={(e) => setQData({ ...qData, key: e.target.value })}
              allowClear
            />
          </Form.Item>
        </Col>
        <Col span={3}>
          <Form.Item style={{ marginBottom: 0 }}>
            <Button type="primary" htmlType="submit">
              Search
            </Button>
          </Form.Item>
        </Col>
        <Col span={5}>
          {selected.length ? (
            <Popconfirm
              title="Are you sure to delete these selected Order?"
              onConfirm={() => {
                deleteData(selected);
              }}
              okText="Yes"
              cancelText="No"
            >
              <Button
                type="primary"
                danger
                style={{ float: "right" }}
                icon={<DeleteOutlined />}
              >
                Delete Selected
              </Button>
            </Popconfirm>
          ) : null}
        </Col>
      </Row>
    </Form>
  );
}

const AttributeModal = forwardRef((props, ref) => {
  const [open, setOpen] = useState(false);
  const [data, setData] = useState({ aspect: 1 });

  useImperativeHandle(ref, () => ({
    openForm(dt) {
      setOpen(true);
      setData(dt);
    },
  }));

  return (
    <>
      <Modal
        title={
          <>
            Details of Order:{" "}
            <Typography.Text type="danger">{data._id}</Typography.Text>
          </>
        }
        style={{ top: 20 }}
        open={open}
        okText="Save"
        onCancel={() => {
          setOpen(false);
        }}
        maskClosable={false}
        width={1200}
        destroyOnClose
        className="app-modal-body-overflow"
        footer={null}
      >
        <OrderDetail {...{ type: data.type, orderId: data._id }} />
      </Modal>
    </>
  );
});
