/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Input, Tag } from "antd";
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from "@ant-design/icons";
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from "react";
import Pagination from "../../components/Pagination";
import { AntdMsg } from "../../../utils/Antd";
import service from "../../../services/job";

export default function JobApplication({ parentId }) {
	const [data, setData] = useState([]);
	const [loading, setLoading] = useState(false);
	const [selected, setSelected] = useState([]);
	const [qData, setQData] = useState({ page: 1, limit: 20, fileType: "photo", parentId });

	const columns = [
		{
			title: "#",
			width: 40,
			render: (v, row, i) => <b>{i + 1}</b>,
		},
		{
			title: "Designation",
			dataIndex: "jobDetails",
			render: v => v?.name
		},
		{
			title: "Applicant Name",
			dataIndex: "name",
			// width: 100,
		},
		{
			title: "Applicant Phone",
			dataIndex: "phone",
			// width: 100,
		},
		{
			title: "Applicant Message",
			dataIndex: "message",
			// width: 100,
		},
		{
			title: "Applicant Email",
			dataIndex: "email",
			// width: 100,
		},
		{
			title: "Applicant CV",
			dataIndex: "cv",
			// width: 100,
			render: v => v?.url ? <Button type="text"><a href={v?.url} target="_blank">Open CV</a></Button> : 'No CV'
		},
		{
			title: "Action",
			dataIndex: "_id",
			width: 70,
			render: (v, row) => {
				return (
					<>
						<Popconfirm
							title="Are you sure to delete this application?"
							onConfirm={() => {
								deleteData(row._id);
							}}
							okText="Yes"
							cancelText="No"
						>
							<Button
								type="primary"
								size="small"
								danger
								icon={<DeleteOutlined />}
							/>
							&nbsp;
						</Popconfirm>
					</>
				);
			},
		},
	].filter((item) => !item.hidden);

	function list() {
		setLoading(true);
		service.listJobApplication(qData).then((res) => {
			setData(res.data?.map((v) => ({ ...v, key: v._id })));
			setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
		}).finally(() => {
			setLoading(false);
		});
	}
	const deleteData = (id) => {
		service.deleteJobApplication(id)
			.then((res) => {
				AntdMsg(res.message);
				list();
				setSelected([]);
			})
			.catch((err) => {
				AntdMsg(err.message, "error");
			});
	};
	const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));
	tableColumns[0].fixed = true;
	tableColumns[tableColumns.length - 1].fixed = "right";
	const tableProps = {
		bordered: true,
		loading,
		size: "small",
		title: () => (
			<Search
				{...{ selected, deleteData, qData, setQData, list }}
			/>
		),
		showHeader: true,
		footer: () => <Pagination {...{ qData, setQData }} />,
		rowSelection: {
			onChange: (selectedRowKeys) => {
				setSelected(selectedRowKeys);
			},
		},
		tableLayout: undefined,
	};
	useEffect(() => {
		list();
	}, [qData.page, qData.limit]);

	return (
		<>
			{!parentId ? (
				<Typography.Title level={5} style={{ marginTop: 0 }}>
					List of Jobs
				</Typography.Title>
			) : null}
			<Table
				{...tableProps}
				pagination={{ position: ["none"], pageSize: qData.limit }}
				columns={tableColumns}
				dataSource={data.length ? data : []}
				scroll={{ y: "calc(100vh - 340px)", x: "calc(100vw - 387px)" }}
			/>
		</>
	);
}
function Search({ selected, deleteData, qData, setQData, list, }) {
	const onChange = (v, key) => {
		qData[key] = v;
		if (v === undefined || v === "") {
			delete qData[key];
			list();
		}
	};
	return (
		<Form onSubmitCapture={list} className="search-form">
			<Row gutter={[12, 2]}>
				<Col span={4}>
					<Form.Item style={{ marginBottom: 0 }}>
						<Input
							placeholder="Search by applicant name / phone / email"
							value={qData.key}
							onChange={(e) => onChange(e.target.value, "key")}
							allowClear
						/>
					</Form.Item>
				</Col>
				<Col span={3}>
					<Form.Item style={{ marginBottom: 0 }}>
						<Button type="primary" htmlType="submit">
							Search
						</Button>
					</Form.Item>
				</Col>
				<Col span={5}>
					{selected.length ? (
						<Popconfirm
							title="Are you sure to delete these selected applications ?"
							onConfirm={() => {
								deleteData(selected);
							}}
							okText="Yes"
							cancelText="No"
						>
							<Button
								type="primary"
								danger
								style={{ float: "right" }}
								icon={<DeleteOutlined />}
							>
								Delete Selected
							</Button>
						</Popconfirm>
					) : null}
				</Col>
			</Row>
		</Form>
	);
}