/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Input, Tag, DatePicker, Divider, Badge } from "antd";
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined, CheckCircleFilled, CloseCircleFilled } from "@ant-design/icons";
import { useEffect, useState, forwardRef, useImperativeHandle, useRef } from "react";
import service from "../../../services/coupon";
import UserService from "../../../services/user";
import Pagination from "../../components/Pagination";
import { AntdMsg } from "../../../utils/Antd";
import moment from "moment";
import dayjs from "dayjs";
import util from "../../../utils/util";

export default function Coupon() {
	const [data, setData] = useState([]);
	const [loading, setLoading] = useState(false);
	const [selected, setSelected] = useState([]);
	const [qData, setQData] = useState({ page: 1, limit: 20 });
	const [user, setUser] = useState([]);
	const addNewModalRef = useRef();

	const columns = [
		{
			title: "Code",
			dataIndex: "code",
			width: 150,
			render: (v, row) => row.isApplied
				? <Badge count={<CheckCircleFilled style={{color: 'green'}}/>}><Tag>{v}</Tag></Badge> 
				: new Date(row.expiryDate) < new Date()
					? <Badge count={<CloseCircleFilled style={{color: 'red'}}/>}><Tag>{v}</Tag></Badge> 
					: <Tag>{v}</Tag>
		},
		{
			title: "Title",
			dataIndex: "title",
		},
		{
			title: "User",
			dataIndex: "userDetails",
			width: 250,
			render: (v) => (v.name || 'UNKNOWN')
		},
		{
			title: "Discount Value",
			dataIndex: "discountValue",
			width: 120,
			render: (v, row) => row.discountType === 'flat' ? <b> {v}</b> : <b>{v} %</b>
		},
		{
			title: "Discount Type",
			dataIndex: "discountType",
			width: 120,
			render: (v) => <b> {v.toUpperCase()}</b>
		},
		{
			title: "Expiry Date",
			dataIndex: "expiryDate",
			width: 150,
			render: (v) => <span>{moment(v).format("DD MMM YYYY")}</span>,
		},

		{
			title: "Status",
			dataIndex: "status",
			width: 80,
			render: (isActive) => {
				if (isActive) {
					return <Tag color="green">Active</Tag>;
				} else {
					return <Tag color="red">Inactive</Tag>;
				}
			},
		},
		{
			title: "Action",
			dataIndex: "_id",
			width: 70,
			render: (v, row) => {
				return (
					<>
						<Popconfirm
							title="Are you sure to delete this User?"
							onConfirm={() => {
								deleteData(row._id);
							}}
							okText="Yes"
							cancelText="No"
						>
							<Button
								type="primary"
								size="small"
								danger
								icon={<DeleteOutlined />}
							/>
							&nbsp;
						</Popconfirm>
						<Button
							type="primary"
							size="small"
							icon={<EditOutlined />}
							onClick={() => {
								addNewModalRef.current.openForm(row);
							}}
						/>
					</>
				);
			},
		},
	].filter((item) => !item.hidden);

	function list() {
		setLoading(true);
		service
			.list(qData)
			.then((res) => {
				setData(res.data?.map((v) => ({ key: v._id, ...v })).reverse());
				setQData({
					...qData,
					limit: res.extra.limit,
					page: res.extra.page,
					total: res.extra.total,
				});
			})
			.catch((err) => { })
			.finally(() => {
				setLoading(false);
			});
	}

	function listUser() {
		UserService.listUser()
			.then((res) => {
				setUser(res.data || []);
			})
			.catch((err) => {
				AntdMsg("User data not loaded", "error");
			});
	}

	const deleteData = (id) => {
		service
			.delete(id)
			.then((res) => {
				AntdMsg(res.message);
				list();
				setSelected([]);
			})
			.catch((err) => {
				AntdMsg(err.message, "error");
			});
	};

	const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

	tableColumns[0].fixed = true;
	tableColumns[tableColumns.length - 1].fixed = "right";

	const tableProps = {
		bordered: true,
		loading,
		size: "small",
		title: () => (
			<Search
				{...{ addNewModalRef, selected, deleteData, qData, setQData, list }}
			/>
		),
		showHeader: true,
		footer: () => <Pagination {...{ qData, setQData }} />,
		rowSelection: {
			onChange: (selectedRowKeys) => {
				setSelected(selectedRowKeys);
			},
		},
		tableLayout: undefined,
	};

	useEffect(() => {
		list();
		listUser();
	}, [qData.page, qData.limit]);

	return (
		<>
			<Typography.Title level={5} style={{ marginTop: 0 }}>
				List of Coupons
			</Typography.Title>
			<Table
				{...tableProps}
				pagination={{ position: ["none"], pageSize: qData.limit }}
				columns={tableColumns}
				dataSource={data.length ? data : []}
				scroll={{ y: "calc(100vh - 340px)", x: "calc(100vw - 387px)" }}
			/>
			<AddForm ref={addNewModalRef} {...{ list, user }} />
		</>
	);
}

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list }) {
	const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined || v === "") {
          delete qData[key];
          list();
        }
      };
	return (
		<Form onSubmitCapture={list} className="search-form">
			<Row gutter={[12, 2]}>
				<Col span={4}>
					<Form.Item style={{ marginBottom: 0 }}>
						<Input
							placeholder="Search by name "
							value={qData.key}
							onChange={(e) => onChange(e.target.value,"key")}
							allowClear
						/>
					</Form.Item>
				</Col>
				<Col span={3}>
					<Form.Item style={{ marginBottom: 0 }}>
						<Button type="primary" htmlType="submit">
							Search
						</Button>
					</Form.Item>
				</Col>
				<Col span={5}>
					<Form.Item style={{ marginBottom: 0, float: "right" }}>
						<Button
							type="primary"
							icon={<PlusOutlined />}
							onClick={() => {
								addNewModalRef.current.openForm();
							}}
						>
							Add New
						</Button>
					</Form.Item>
					{selected.length ? (
						<Popconfirm
							title="Are you sure to delete these selected images?. Be sure to delete the image from server."
							onConfirm={() => {
								deleteData(selected);
							}}
							okText="Yes"
							cancelText="No"
						>
							<Button
								type="primary"
								danger
								style={{ float: "right" }}
								icon={<DeleteOutlined />}
							>
								Delete Selected
							</Button>
						</Popconfirm>
					) : null}
				</Col>
			</Row>
		</Form>
	);
}

const AddForm = forwardRef((props, ref) => {
	const { list, user } = props;
	const [open, setOpen] = useState(false);
	const [data, setData] = useState({ aspect: 1 });
	const [ajxRequesting, setAjxRequesting] = useState(false);

	const handleChange = (value) => {
		setData({ ...data, ...value });
	};

	useImperativeHandle(ref, () => ({
		openForm(dt) {
			setOpen(true);
			setData(dt ? { ...dt } : { status: true, expiryDate: new Date() });
		},
	}));


	const save = () => {
		setAjxRequesting(true);
		service
			.save(data)
			.then((res) => {
				AntdMsg(res.message);
				handleChange({ ...res.data });
				list();
				setOpen(false);
			})
			.catch((err) => {
				if (typeof err.message === "object") {
					let dt = err.message[Object.keys(err.message)[0]];
					AntdMsg(dt, "error");
				} else {
					AntdMsg(err.message, "error");
				}
			})
			.finally(() => {
				setAjxRequesting(false);
			});
	};

	return (
		<>
			<Modal
				title={(!data._id ? "Add" : "Edit") + " Coupon"}
				style={{ top: 20 }}
				open={open}
				okText="Save"
				onOk={save}
				okButtonProps={{ disabled: ajxRequesting }}
				onCancel={() => { setOpen(false); }}
				destroyOnClose
				maskClosable={false}
				width={800}
				className="app-modal-body"
				footer={[
					<Button
						key="cancel"
						onClick={() => {
							setOpen(false);
						}}
					>
						Cancel
					</Button>,
					<Button key="save" type="primary" onClick={save}>
						Save
					</Button>,
				]}
			>
				<Spin
					spinning={ajxRequesting}
					indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}
				>
					<Form layout="vertical">
						<Row gutter={[12, 2]}>
							<Col span={16}>
								<Form.Item label="User" required>
									<Select
										value={data.userId}
										onChange={(v) => {
											handleChange({ userId: v });
										}}
										options={user.map((v) => ({
											value: v._id,
											label: v.name,
										}))}
									></Select>
								</Form.Item>
							</Col>
							<Col span={8}>
								<Form.Item label="Code" required>
									<Input placeholder="Code" value={data.code} onChange={(e) => { handleChange({ code: util.removeSpecialChars(e.target.value)?.toUpperCase() }); }} />
								</Form.Item>
							</Col>

							<Col span={8}>
								<Form.Item label="Title" required>
									<Input placeholder="Title" value={data.title} onChange={(e) => { handleChange({ title: e.target.value }) }} />
								</Form.Item>
							</Col>

							<Col span={16}>
								<Form.Item label="Description" required>
									<Input placeholder="Description" value={data.description} onChange={(e) => { handleChange({ description: e.target.value }); }} />
								</Form.Item>
							</Col>

							<Col span={8}>
								<Form.Item label="Expiry Date" required>
									<DatePicker
										format='DD MMM YYYY'
										style={{ width: "100%" }}
										value={data.expiryDate && dayjs(moment(data.expiryDate).format("YYYY-MM-DD"))}
										onChange={(v) => { handleChange({ expiryDate: new Date(v) }) }}
										allowClear
									/>
								</Form.Item>
							</Col>

							<Col span={8}>
								<Form.Item label="Discount Type" required>
									<Select
										value={data.discountType}
										onChange={(e) => {
											handleChange({ discountType: e });
										}}
									>
										<Select.Option value={"percent"}>Percent</Select.Option>
										<Select.Option value={"flat"}>Flat</Select.Option>
									</Select>
								</Form.Item>
							</Col>

							<Col span={8}>
								<Form.Item label="Discount Value" required>
									<Input
										placeholder="Discount Amount"
										value={data.discountValue}
										onChange={(e) => {
											handleChange({ discountValue: e.target.value });
										}}
									/>
								</Form.Item>
							</Col>
							<Col span={24}>
								<Form.Item label="TC" required>
									<Input.TextArea placeholder="Enter Tc" value={data.termAndCondition} onChange={(e) => handleChange({ termAndCondition: e.target.value })} />
								</Form.Item>
							</Col>
							<Divider />
							<Col span={8}>
								<Form.Item label="Max Discount Value">
									<Input
										placeholder="Max Discount Amount"
										value={data.maxDiscountValue}
										onChange={(e) => {
											handleChange({ maxDiscountValue: e.target.value });
										}}
									/>
								</Form.Item>
							</Col>
							<Col span={8}>
								<Form.Item label="Minimun Order Value">
									<Input
										placeholder=" Minimun Order Amount"
										value={data.minimunOrderPrice}
										onChange={(e) => {
											handleChange({ minimunOrderPrice: e.target.value });
										}}
									/>
								</Form.Item>
							</Col>

							<Col span={8}>
								<Form.Item label="Credit End Date (if)">
									<DatePicker
										format='DD MMM YYYY'
										style={{ width: "100%" }}
										value={data.creditEndDate && dayjs(moment(data.creditEndDate).format("YYYY-MM-DD"))}
										onChange={(v) => { handleChange({ creditEndDate: new Date(v) }) }}
										allowClear
									/>
								</Form.Item>
							</Col>
							<Col span={8}>
								<Form.Item label="Status" required>
									<Select value={data.status} onChange={(e) => { handleChange({ status: e }); }} >
										<Select.Option value={true}>Active</Select.Option>
										<Select.Option value={false}>Inactive</Select.Option>
									</Select>
								</Form.Item>
							</Col>
						</Row>
					</Form>
				</Spin>
			</Modal>
		</>
	);
});